//Group Information (optional) gray
#group.postbox h2.ui-sortable-handle span span { color: #ccc; }

//Import & Settings page
body.tsml_meeting_page_import {
	
	ol.notice-error {
		padding: 10px 10px 8px 45px;
		li { margin: 0 0 2px; }
	}
	
	.progress {
		height: 20px;
		background-color: white;
		-webkit-box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);
		box-shadow: 0 1px 1px 0 rgba(0,0,0,.1);
		overflow: hidden;
		margin: 5px 0 15px;
		
		.progress-bar {
			background-color: #bbb;
			color: white;
			text-align: center;
			overflow: hidden;
			width: 0;
			height: 100%;
			float: left;
			line-height: 20px;
			font-size: 12px;
			-webkit-transition: width .6s ease;
			-o-transition: width .6s ease;
			transition: width .6s ease;
		}
	}
	
	//meeting types legend
	.postbox {
		ul.types {
			margin-top: 10px; overflow: auto;
			-webkit-columns: 3 auto; -moz-columns: 3 auto; columns: 3 auto;
			li { margin-bottom: 0; }
		}
		p:last-child { margin-bottom: 0; }
		
		//get feedback & get notified
		&.admin_contacts {
			table {
				margin: 0 0 15px; width: 100%; border: 0; border-spacing: 0; border-collapse: collapse;
				tr {
					td {
						margin: 0;
						padding: 4px;
						border: 0;
						border-bottom: 1px solid #ccc;
						&:first-child {
							font-family: Consolas,Monaco,monospace;
						}
						&:last-child {
							text-align: right;
							span { cursor: pointer; }
						}
					}
					&:first-child td {
						padding-top: 0;
					}
					&:last-child td {
						padding-bottom: 0;
						border-bottom: 0;
					}
				}
			}
			
			//add email forms
			form {
				overflow: auto;
				input {
					box-shadow: none;
					width: 60px;
					float: left;
					&[type=email] {
						border-radius: 3px;
						border: 1px solid #ccc;
						font-size: 13px;
						line-height: 26px;
						margin: 0;
						height: 28px;
						width: 180px;
						margin-right: 10px;
						font-family: Consolas,Monaco,monospace;
					}
				}
			}
		}
		
		//where's my info list margins
		&#wheres_my_info {
			ul {
				margin-top: -10px;
				li { margin: 0 0 2px; }
			}
		}
		
		//try the apps app store buttons
		&#try_the_apps {
			p.buttons {
				margin-left: -5px; margin-right: -5px; overflow: auto;
				a {
					padding: 0 5px; width: 50%; float: left; display: block; box-sizing: border-box;
					img { width: 100%; height: auto; }
				}
			}
		}

	}
	
}

//Meeting Add / Edit page
.wp-admin .meta_form_row { 
	padding: 10px 0 0; overflow: auto; clear: left;
	label, input, div.checkboxes, #map { float: left; line-height: 100%; } 
	label { width: 13%; margin-right: 2%; text-align: right; font-size: 15px; line-height: 1.4; margin-top: 9px; white-space: nowrap; }
	input[type=text], input[type=date], select, textarea, div.checkboxes, #map { 
		width: 85%; margin: 0; font-size: 18px; padding: 6px; height: 40px; line-height: normal; float: left;
		border-radius: 0;
	}
	input.time {
		width: 42%; visibility: hidden;
		&.ui-timepicker-input { visibility: visible; }
		&:last-child { margin-left: 1%; }
	}
	input, select { 
		border: 1px solid #ccc; 
	    border-radius: 0;
	    -webkit-border-radius: 0;
	}
	input[type=radio], input[type=checkbox] { 
		float: left;
		&:focus { box-shadow: none; }
	}
	input[disabled] { opacity: .5; cursor: not-allowed; }

	&.typeahead {
		overflow: visible;
		.twitter-typeahead{
		     width: 85%;
		     input { width: 100%; }
		}
		.tt-hint { color: #999; }
		.tt-menu { display: none !important; }
	}
	
	&.checkbox {
		padding-left: 15%;
		line-height: 1;
		label { 
			width: auto;
			margin: 0; 
			font-size: 13px;
			input { margin: 1px 7px 0 0; }
		}
	}

	textarea { height: 140px; }
	&:last-child { margin-bottom: 0; }
	div.checkboxes { 
		overflow: auto; padding: 0; height: auto; 
		-webkit-columns: 3 auto;
		-moz-columns: 3 auto;
		columns: 3 auto;
		
		input { margin: 0 8px 0 0; }
		label { float: none; display: block; margin: 0; padding: 5px 0; line-height: 16px; width: 100%; text-align: left; font-size: 13px; }
	}
	&.city {
		input { width: 60%; margin-right: 1%; }
		select { width: 24%; }
	}

	::-webkit-input-placeholder	{ color: #ccc }
	:-moz-placeholder			{ color: #ccc; }
	::-moz-placeholder			{ color: #ccc; }
	:-ms-input-placeholder		{ color: #ccc; }


	#map { background-color: #f6f6f6; height: 300px; padding: 0; }
	
	//meetings at a location
	ol {
		overflow: auto;
		font-size: 15px;
		margin: 0;
		padding: 10px 0 0 0;
		-webkit-columns: 2 auto;
		-webkit-column-gap: 60px;
		li {
			break-inside: avoid-column;
			-webkit-column-break-inside: avoid;
			padding: 0 0 10px 110px;
			position: relative;
			margin: 0 0 0 25px;
			span { color: #999; display: inline-block; position: absolute; left: 0; }
			&:last-child { margin-bottom: 0; }
		}
	}
	
	.container {
		width: 85%;
		float: left; 
		display: table;
		.row {
			display: table-row;
			&:first-child div { border-top: 0;	}
			div {
				border-left: 10px solid transparent;
				border-top: 10px solid transparent;
				display: table-cell;
				&:first-child { border-left: 0;	}
				input { width: 100%; }
			}
		}
	}
}